//  props
//  @click  isCollapsed

<style lang="less" scoped>
  div {
    display: inline-block;
    width: 1.2em;
    height: 100%;
    position: relative;
    vertical-align: bottom;
    span {
      display: flex;
      align-items:center;
      justify-content: center;
      height: 100%;
      width: 100%;
      span {
        border: 1px solid #888;
        border-left: none;
        border-bottom: none;
        width: 0.6em;
        height: 0.6em;
      }
      span.top {
        transform: rotate(-45deg);
        margin-top: 0.3em;
      }
      span.right {
        transform: rotate(45deg);
        margin-left: -0.3em;
      }
      span.bottom {
        transform: rotate(135deg);
        margin-top: -0.3em;
      }
      span.left {
        transform: rotate(225deg);
        margin-left: 0.3em;
      }
    }
  }
  .cursor {
    cursor: pointer;
  }
  .slide {
    transition: transform 0.3s, margin 0.3s;
  }
</style>

<template>
  <div :class="{'cursor': cursor}">
    <span><span :class="spanClassName" :style="style.span"></span></span>
  </div>
</template>

<script>
export default {
  name: 'Arrows',
  data () {
    return {
      start: 'right',
      end: 'bottom',
      style: {
        span: {
          borderTop: this.borderWidth + ' solid ' + this.borderColor,
          borderRight: this.borderWidth + ' solid ' + this.borderColor,
          width: this.size,
          height: this.size
        }
      }
    }
  },
  props: {
    borderColor: { // 
      type: String,
      default: '#555'
    },
    borderWidth: { // 
      type: String,
      default: '0.07em'
    },
    size: { // &
      type: String,
      default: '0.6em'
    },
    // top/right/bottom/left
    // '-'
    rotate: {
      type: String,
      default: 'right-bottom'
    },
    slide: Boolean, // 
    cursor: Boolean, // hover
    isCollapsed: Boolean // 
  },
  computed: {
    spanClassName () {
      let className = {'slide': this.slide}
      if (this.end === '') {
        className[this.start] = true
      } else {
        className[this.start] = this.isCollapsed
        className[this.end] = !this.isCollapsed
      }
      return className
    }
  },
  created () {
    this.check_rotate()
  },
  methods: {
    check_rotate () {
      let reg = /^top$|^right$|^bottom$|^left$/
      if (this.rotate.indexOf('-') !== -1) {
        let list = this.rotate.split('-')
        if (reg.test(list[0]) && reg.test(list[1])) {
          this.start = list[0]
          this.end = list[1]
        }
      } else {
        if (reg.test(this.rotate)) {
          this.start = this.rotate
          this.end = ''
        }
      }
    }
  }
}
</script>



