/* api */

// 
export const loginApi = (that) => {
  const data = {
    name: that.name,
    password: that.password
  }
  that.$store.dispatch('login', { data }).then((res) => {
    if (res.data.code === 200) {
      that.$router.push('/')
    } else {
      console.error(res)
    }
  }).catch((res) => {
    console.error(res)
  })
}

// 
export const logoutApi = (that) => {
  that.$store.dispatch('logout').then((res) => {
    if (res.data.code === 200) {
      that.$router.push('/login')
    } else {
      console.error(res)
    }
  }).catch((res) => {
    console.error(res)
  })
}



