<style lang="less" scoped>
.login {
  background: #96a066;
  .loginCard {
    background: rgba(255,255,255,0.9);
    border: 1px solid #fff;
    border-radius: 4px;
    color: #888;
    width: 400px;
    padding: 10px 0;
    text-align: center;
    position: relative;
    top: -10%;
    h2 {
      // color: #fff;
      font-size: 1.6em;
      margin: 10px 0;
    }
    .row {
      width: 60%;
      margin: 10px;
    }
  }
}
</style>

<template>
  <div class="login flex full">
    <section class="loginCard">
      <header>
        <h2></h2>
      </header>
      <Input class="row" size="large" type="text" clearable
        @on-change="(e)=>{recodeName(e)}" placeholder="" />
      <Input class="row" size="large" type="password" clearable
        @on-change="(e)=>{recodePassword(e)}" placeholder="" />
      <Button class="row" size="large" long @click="loginAsk"> </Button>
    </section>
  </div>
</template>

<script>
import { loginApi } from '@/api'

export default {
  name: 'Login',
  data () {
    return {
      name: '',
      password: '',
      loading: false
    }
  },
  computed: {},
  methods: {
    recodeName (e) {
      this.name = e.target.value
    },
    recodePassword (e) {
      this.password = e.target.value
    },
    loginAsk () {
      loginApi(this)
    }
  }
}
</script>



